
import Search from 'vux/src/components/search/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XCircle from 'vux/src/components/x-circle/index.vue'

import Pages from '../../components/Pages/Pages';
export default {
name: 'Join',
components: {
Search,
Tab,
TabItem,
XCircle,
Pages
},
async created() {
await this.getData();

},
data() {
return {
percent:100,
joinOrderList:[],
queryUserId:"",
tab: '2',
totalCount: 0, // 总条数
pageSize: 10, // 查询条数
startRow: 0 // 起始条数
};
},
methods: {
onSubmit() {
this.getData();
},
onBlur() {
this.getData();
},
onTabClick(i) {
this.tab=i;
this.totalCount= 0;
this.startRow= 0;
this.getData();
},

async getData(v) {
this.$vux.loading.show({
text: 'Loading'
});
if (v != undefined) {
this.startRow = v;
}
let res = await this.$http('/queryJoinOrderInfo', {
body: {
startRow: this.startRow,
pageSize: this.pageSize,
isPC:0,
searchId:this.queryUserId,
state:this.tab
}
});
console.log(res);
this.joinOrderList = res.returnList;
this.totalCount = res.totalCount;
this.$vux.loading.hide();
}

}
};



